import React from "react";
import { GatsbyImage, getImage } from "gatsby-plugin-image";
import * as styles from './Book.module.css'
import { Link } from "gatsby";

const Book = ({book}) => {
    console.log(book)
    const image = getImage(book.cover.localFile)
    const bookNumber = `Book ${book.bookNumber} of ${book.numberOfBooks}`;
    const url = (`${book.name}-${book.subheading}`).replaceAll(" ", "-")
    console.log(url)
    return(
        <Link href={url} className={styles.book}>
            <GatsbyImage className={styles.cover} objectFit="scale-down" image={image}/>
            <div className={styles.info}>
                <p>{book.name}</p>
                <p>{book.subheading}</p>
                <p>{bookNumber}</p>        
            </div>
        </Link>
    )

}

export default Book;

