import React from 'react'
import * as styles from './Books.module.css'
import Book from './Book'

const Books = ({academy}) => {

    return (
        <section className={styles.academy}>
            <h2 className={`center`}>{academy.nodes[0].academy.name}</h2>
            <div className={styles.bookContainer}>
                {academy.nodes.map((book, index) => <Book book={book} key={index}/>)}
            </div>
        </section>
    )

}

export default Books;