import React from "react";
import Books from '../books/Books';
import { graphql, useStaticQuery } from "gatsby";
import * as styles from './Learning.module.css'
import Back from "../back/Back";

const LearningHubPage = () => {
    const data = useStaticQuery(graphql`
    query MyQuery {
      allStrapiBook {
        group(field: academy___name) {
          nodes {
            bookNumber
            name
            numberOfBooks
            subheading
            academy {
              name
            }
            cover {
              localFile {
                childImageSharp {
                  gatsbyImageData
                }
              }
            }
          }
        }
      }
    }
    `)
    const academies = data.allStrapiBook.group;

    return(
        <article className={styles.bookContainer}>
            {academies.map((academy, index)=> <Books academy={academy} key={index}/>
            )}
        </article>
    )
}

export default LearningHubPage;